<template>
  <PcDetail
	:data="detail"
  ></PcDetail>
</template>

<script>
	import PcDetail from '../components/pc-detail/pc-detail.vue'
export default {
  name: 'pc-detail',
  props: {
  },
  data() {
    return {
		detail:{}
	};
  },
  components: {PcDetail},
  mounted() {
	  let collectionName=this.$route.query.collectionName;
	  let _id =this.$route.params._id;
	  this.$axios({
		  url:`/api/${collectionName}/${_id}`
	  }).then(res=>{
		  // console.log(res)
		  this.detail=res.data.data
	  })
  },
  updated() {},
  methods: {}
};
</script>

