<template>
  <div>
	  <div class="header">
	      <h3>足球圈-注册</h3>
	  </div>
	  <div class="content">   
	      <form action="">
	            <pc-input class="mar" v-model="username"  label="账号"></pc-input>
	          <pc-input type="password" class="mar" v-model="password"  label="密 码"></pc-input>
			    <pc-input class="mar" v-model="phonenumber"  label="手机号"></pc-input>
			   <pc-button text="注 册" @click="reg" bg-color="green" class="btn"></pc-button>
			     <router-link class="box" to="/login" tag="div">已经注册,点击登录</router-link>
	      </form>
	  
	  </div>
  </div>
</template>

<script>
	import PcButton from '../components/pc-button/button.vue'
	import PcInput from '../components/pc-input/pc-input.vue'
export default {
  name: 'reg',
  props: {
  },
  data() {
    return {
		username:'',
		password:'',
		phonenumber:''
	};
  },
  components: {
	  PcButton,PcInput
  },
  mounted() {},
  updated() {},
  methods: {
	  reg(){
		  this.$axios({
			  url:'/api/reg',
			  method:'post',
			  data:{
				  username:this.username,
				  password:this.password,
				  phonenumber:this.phonenumber
			  } 
		  }).then(res=>{
			  // console.log(res)
			  this.$router.push('/login')
			  // if(res.data.err)
		  })
	  }
  }
};
</script>

<style scoped>
	.header{
	    width: 100%;
	    height: 0.85rem;
	    background: #0cc440;
	    text-align: center;
	    line-height: 0.85rem;
	}
	.header h3{
	    font-size: 0.26rem;
	    color: #cde6d3;
	}
	.content{
		padding: 0,.5rem;
	}
	.mar{
		margin-top:1rem;
		width: 4.85rem;
		margin-left: 0.85rem;
		/* border: 1px solid #CCCCCC; */
	}
	.btn{
		margin-left: 0.85rem;
		margin-top: 0.85rem;
	}
	.box{
		text-align: center;
		margin-top: 0.2rem;
	}
</style>
