<template>
  <div>
	  <div class="header">
	      <h3>足球圈-登录</h3>
	  </div>
	  <div class="content">   
	      <form action="">
	            <pc-input class="mar" v-model="username"  label="账号"></pc-input>
	          <pc-input class="mar" v-model="password" type="password" label="密 码"></pc-input>
			  {{mess}}
			   <pc-button bg-color="green" class="btn" @click="login" text="登录"></pc-button>
			   <router-link class="box" to="/reg" tag="div">没有注册,点击注册</router-link>
	      </form>
	  
	  </div>
  </div>
</template>

<script>
	import PcButton from '../components/pc-button/button.vue'
	import PcInput from '../components/pc-input/pc-input.vue'
export default {
  name: 'reg',
  props: {
  },
  data() {
    return {
		username:'',
		password:'',
		mess:''
	};
  },
  components: {
	  PcButton,PcInput
  },
 methods:{
      login(){
        this.$axios({
          url:'/api/login',
          method:'post',
          data:{
            username:this.username,
            password:this.password
          }
        }).then(
          res => {
			  // console.log(res)
            if(res.data.err===0){
              //种local  跳转到之前
              window.localStorage.setItem('user',JSON.stringify(res.data));
              if(!this.$route.query.p){
                this.$router.push('/user')
              }else{
                this.$router.push(this.$route.query.p)
              }
            }else{
              this.mess = res.data.msg
            }
          }
        )
      }
    }
};
</script>

<style scoped>
	.header{
	    width: 100%;
	    height: 0.85rem;
	    background: #0cc440;
	    text-align: center;
	    line-height: 0.85rem;
	}
	.header h3{
	    font-size: 0.26rem;
	    color: #cde6d3;
	}
	.content{
		padding: 0,.5rem;
	}
	.mar{
		margin-top:1rem;
		width: 4.85rem;
		margin-left: 0.85rem;
		/* border: 1px solid #CCCCCC; */
	}
	.btn{
		margin-left: 0.85rem;
		margin-top: 0.85rem;
	}
	.box{
		text-align: center;
		margin-top: 0.2rem;
	}
</style>
