<template>
	<div >
		<pc-nav ></pc-nav>
		<div class="content">
			<pc-cont
				v-for="(item,index) of list"
				:key="item._id"
				:data="item"
				collectionName="home"
			></pc-cont>
		</div>
	</div>
</template>

<script>
	import PcNav from '../components/pc-nav/pcnav.vue'
	import PcCont from '../components/pc-cont/pc-cont.vue'
export default {
  name: 'home',
  props: {
  },
  data() {
    return {
		list:[],
	};
  },
  components: {
	 PcNav,PcCont 
  },
  mounted() {
	  this.$axios({
		  url:'/api/home',
		  params:{_limit:20,_page:1}
	  }).then(res=>{
		  this.list=res.data.data
	  })
  },
  updated() {},
  methods: {}
};
</script>

