<template>
  <div id="app">
	<Header v-show="$root.bheader"></Header>
	<router-view></router-view>
	<Footer v-show="$root.bFoot"></Footer>
	<!-- <PcCell></PcCell> -->
	<!-- <Find></Find> -->
	<!-- <Detail></Detail> -->
  </div>
</template>

<script>
// import UcButton from './components/pc-button/button.vue'
// import PcInput from './components/pc-button/pc-input.vue'
// import Detail from '../pages/detail.vue'
import Header from './Header.vue'
import Footer from './Footer.vue'

export default {
  name: 'App',
  data(){
	  return{
		  type:''
	  }
  },
  components: {
   Footer,Header

  },
  methods:{
	  checkRootData(path){
	          if(/login|reg|find|user/.test(path)){
	            this.$root.bheader=false
				this.$root.bFoot=false
	          }else{
				this.$root.bheader=true
				this.$root.bFoot=true  
			  }
	        }
  },
  watch:{
      	$route:{
          handler(nextValue){
            let path = nextValue.path;
            this.checkRootData(path)
          },
        }
      }
}
</script>

